/* 
=====================================================================
button block
=====================================================================
*/
.arrow-btn {
  border-radius: 6px;
  background-clip: padding-box;
  font-size: 1.125em;
  display: inline-block;
  font-weight: 500;
  font-size: 16px;
  padding: 10px 25px;
  border: 1px solid #2638C4;
  background-color: #fff;
  transition: all 0.3s ease 0s;
  white-space: nowrap;
  cursor: pointer;
}
.arrow-btn a {
  cursor: pointer;
}
.arrow-btn svg {
  position: relative;
  transition: right 0.2s ease 0s;
  right: 4px;
}
.arrow-btn:hover {
  box-shadow: -1px 6px 10px 0 rgba(120, 60, 20, 0.2);
  color: #fff;
}
.arrow-btn:hover svg {
  right: 0px;
  position: relative;
}
.arrow-btn svg {
  width: 20px;
  margin-left: 5px;
}
.arrow-btn.white-arrow-btn {
  background-color: #fff;
  color: #2638C4;
}
.arrow-btn.purple-arrow-btn {
  background-color: #2638C4;
  color: #fff;
  border: none;
}
.arrow-btn.purple-arrow-btn svg path {
  fill: #fff;
}
.arrow-btn.blue-arrow-btn {
  background-color: #006cb4;
  color: #fff;
  border: none;
}
.arrow-btn.blue-arrow-btn svg path {
  fill: #fff;
}
.arrow-btn.green-arrow-btn {
  background-color: #33A99D;
  color: #fff;
  border: none;
}
.arrow-btn.green-arrow-btn svg path {
  fill: #fff;
}
.arrow-btn.fb-arrow-btn {
  background-color: #fff;
  color: #006cb4;
  border: none;
  transition: all 0.3s ease 0s;
}
.arrow-btn.fb-arrow-btn svg {
  transition: none !important;
  right: 0px;
}
.arrow-btn.fb-arrow-btn svg path {
  fill: #fff;
}
.arrow-btn.fb-arrow-btn svg:hover {
  box-shadow: -1px 6px 10px 0 rgba(120, 60, 20, 0.2);
  color: #fff;
}
.arrow-btn.fb-arrow-btn svg:hover svg {
  right: 4px !;
  position: relative;
}
.arrow-btn.transparent-arrow-btn {
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
}
.arrow-btn.transparent-arrow-btn svg path {
  fill: #fff;
}
.pc-container a {
  color: #005690;
}
